import React from 'react';

import { Container, Row, Col } from 'react-bootstrap';
import { EnvelopeFill, Whatsapp, Discord } from 'react-bootstrap-icons';
import ScriptTag from 'react-script-tag';

function Footer() {
  return (
    <div>
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 101"><path fill="#1A1A1A" fillOpacity="1" d="M0,100L1440,0L1440,320L0,320Z"></path></svg>
        <div className="pb-2 contentContainer">
            <h2 className="text-center text-white">Contact us </h2>
            <Container className='mt-5'>
                <Row xs={3} md={4} lg={5}>
                    <Col className="ml-auto text-center">
                        <a href="mailto:info@klayhosting.com"><EnvelopeFill color="white" size={96} /></a>
                    </Col>
                    <Col className="text-center">
                        <a href="https://api.whatsapp.com/send?phone=14843824793" rel="noreferrer" target="_blank"><Whatsapp color="white" size={96} /></a>
                    </Col>
                    <Col className="mr-auto text-center">
                        <a href="https://discord.gg/Zd6KxYbnkh" rel="noreferrer" target="_blank"><Discord color="white" size={96} /></a>
                    </Col>
                </Row>
            </Container>
            <div className="pt-5 pb-2 text-center">
                <span className="text-muted">&copy; {new Date().getFullYear()} KLAYHOSTING</span><br />
                <a href="./docs/Terms_and_Conditions.pdf" style={{ textDecoration: 'none' }} rel="noreferrer" target="_blank"><span className="text-muted">TERMS AND CONDITION</span></a>&nbsp;
                <a href="./docs/Privacy_Policy.pdf" style={{ textDecoration: 'none' }} rel="noreferrer" target="_blank"><span className="text-muted">PRIVACY POLICY</span></a>&nbsp;
                <a href="https://klayhosting.statuspage.io/" style={{ textDecoration: 'none' }} rel="noreferrer" target="_blank"><span className="text-muted">STATUS</span></a>
            </div>

            <ScriptTag isHydrating={false} type="text/javascript" src="https://1g84yk8zf391.statuspage.io/embed/script.js" />
        </div>
    </div>
  )
}
  
export default Footer;