import React from 'react';

import { Helmet } from 'react-helmet'
import { Container, Row, Col, Image } from 'react-bootstrap';
import CardsStaff from './CardsStaff';

import servers from './img/about/servers.jpg';
import kevin_staff_card from './img/about/staff_cards/kevin.png';
import alessio_staff_card from './img/about/staff_cards/alessio.png';
import stefan_staff_card from './img/about/staff_cards/stefan.png';

function About() {
  return (
    <div>
      <Helmet>
        <title>About us - KlayHosting</title>
      </Helmet>

      <div className="pt-5 pb-2 contentContainer text-center">
        <span className="h1 text-white">About us</span><br />
        <span className="text-muted">Find out more who we are</span>
      </div>

      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 101"><path fill="#1A1A1A" fillOpacity="1" d="M0,100L1440,0L1440,0L0,0Z"></path></svg>

      <Container className="mb-5">
        <Row>
          <Col xs={12}>
            <div className='mt-4'>
              <h2 className="text-center">Data Center</h2>
              <hr className="mx-auto" style={{ width: '5%' }} />
              <p className="text-center">
                The German-based data center we rely on allows us to guarantee you stability and safety.<br />
                Uninterrupted power supply is key to keep our server up and running. That is why, the Data Center have been investing in their own transformer stations, N+1 UPS and even diesel power generators.<br />
                Servers are only useful when they're reachable from the internet. To ensure that the Data Center have installed at least 2 independent fiber connections.<br />
                The Data Center is equipped with redundant (N+1) chillers and climate cabinets to avoid servers overheat.<br />
                Server security starts with physical security of the Data Center. The Data Center is equipped with CCTV and physical access control. This way they can prevent unauthorized access to any server.<br />
                In our fight for the best price for you we don't forget about the environment. Wherever possible they use modern technologies such as Free Cooling or Groundwater Cooling to increase the energy efficiency. Less energy consumption = lower price!<br />
                Somebody needs to keep an eye on all these servers, so they make sure there are people on the ground in the Data Center 365 days a year including weekends and holidays.
              </p>
              <Image src={servers} fluid />

              <hr />

              <h2 className="mt-5 text-center">Our Team</h2>
              <hr className="mx-auto" style={{ width: '5%' }} />
              <Container>
                <Row>
                  <Col>
                    <CardsStaff img={kevin_staff_card} title="Kevin" role="Founder & DEV" content="Self-employed and motivated entrepreneur with a proven track record of success and extraordinary results in building a company from the simple idea to its actual realization. Experience in all aspects of business development, including finance, operations, technology and marketing. Well-developed leadership / communication skills, excellent time management skills and great teamwork skills." tag="@Klay4#0909" />
                  </Col>
                  <Col>
                    <CardsStaff img={alessio_staff_card} title="Alessio" role="Co-founder & DEV" content="Expert and innovative programmer capable of creating computer codes and effectively managing internal software systems. Ability to collaborate with designers and content producers, creating code to produce multi-threaded server applications. Experience in testing and documentation of software for different applications. Dedication in teamwork and a motivated professional." tag="@ale3497#0344" />
                  </Col>
                  <Col>
                    <CardsStaff img={stefan_staff_card} title="Stefan" role="Support Team Manager" content="Dedicated and experienced customer service professional with several years of experience working in the customer service industry for office technology. Ability to work in fast-paced and demanding environments to provide customers with the highest level of customer service possible. Goal orientation and commitment to finding solutions to any problem. Proven track record of maintaining a strong customer base and attracting new customers." tag="@Killmee1#3276" />
                  </Col>
                </Row>
              </Container>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  )
}

export default About;