import React from 'react';

import {Link} from 'react-router-dom';
import { Navbar, Nav } from 'react-bootstrap';
import CloudIcon from '@material-ui/icons/Cloud';

function NavBar() {
  return (
    <div>
      <Navbar expand="lg" variant="dark" style={{ backgroundColor: '#1A1A1A' }}>
        <Navbar.Brand>
          <Link to="/" style={{ textDecoration: 'none' }}>
            <CloudIcon className="d-inline-block align-top text-white " style={{ width: 30, height: 30 }} />
            <span className="h4 text-white"> KlayHosting</span>
          </Link>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ml-auto">
            <Nav.Link>
              <Link to="/" style={{ textDecoration: 'none' }}>
                <li className="h4 text-white">Home</li>
              </Link>
            </Nav.Link>
            <Nav.Link href="https://billing.klayhosting.com" className="h4 text-white">Shop</Nav.Link>
            <Nav.Link href="https://panel.klayhosting.com" className="h4 text-white">Panel</Nav.Link>
            <Nav.Link href="https://mysql.klayhosting.com" className="h4 text-white">Database</Nav.Link>
            <Nav.Link>
              <Link to="/about" style={{ textDecoration: 'none' }}>
                <li className="h4 text-white">About us</li>
              </Link>
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    </div>
  )
}
  
export default NavBar;