import React from 'react';

import { Card, ListGroup, ListGroupItem, Button } from 'react-bootstrap';

function CardsPricing(props) {
  return (
    <div>
      <Card text='white' className="mx-auto cardEdit text-center" style={{ width: '15rem', backgroundColor: '#1A1A1A' }}>
        <Card.Body>
          <Card.Title>{props.title}</Card.Title>
          <Card.Text style={{ fontSize: '40px'}}>â‚¬{props.price}/mo</Card.Text>
          <Card.Text>{props.desc}</Card.Text>
        </Card.Body>
        <ListGroup className="list-group-flush">
          <ListGroupItem style={{ backgroundColor: '#1A1A1A', borderBlockColor: 'white' }}>{props.ram} RAM</ListGroupItem>
          <ListGroupItem style={{ backgroundColor: '#1A1A1A', borderBlockColor: 'white' }}>{props.disk} SSD</ListGroupItem>
          <ListGroupItem style={{ backgroundColor: '#1A1A1A', borderBlockColor: 'white' }}>{props.db} Database</ListGroupItem>
          <ListGroupItem style={{ backgroundColor: '#1A1A1A', borderBlockColor: 'white' }}>{props.backup} Backup</ListGroupItem>
        </ListGroup>
        <Card.Body>
          <Card.Link href="https://billing.klayhosting.com/servers"><Button variant="light">Buy Now</Button></Card.Link>
        </Card.Body>
      </Card>
    </div>
  )
}

export default CardsPricing;