import React from 'react';

import { Card } from 'react-bootstrap';

function CardsStaff(props) {
  return (
    <div>
        <Card className="mx-auto cardEdit" style={{ width: '18rem' }}>
            <Card.Img variant="top" src={props.img} />
            <Card.Body>
                <Card.Title>{props.title}</Card.Title>
                <Card.Subtitle className="mb-2 text-muted">{props.role}</Card.Subtitle>
                <Card.Text>{props.content}</Card.Text>
            </Card.Body>
            <Card.Footer>
                <small className="text-muted">{props.tag}</small>
            </Card.Footer>
        </Card>
    </div>
  )
}

export default CardsStaff;